import Vue from 'vue'
import App from './App.vue'
import CSS from './components/CSS/CSS.vue'
import JS from './components/JS/JS.vue'
import Closure from './components/JS/Closure.vue'
import Prototype from './components/JS/Prototype.vue'
import EventLoop from './components/JS/EventLoop.vue'
import ToolFunction from './components/JS/ToolFunction.vue'

import About from './components/About.vue'

import Layout from './components/CSS/Layout.vue'
import Animation from './components/CSS/Animation.vue'
import Optimization from './components/CSS/Optimization.vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

Vue.config.productionTip = false

const routes = [
  { path: '/', redirect: '/about' },
  { path: '/css', redirect: '/css/optimization' },
  { path: '/js', redirect: 'js/closure' },
  { path: '/about', component: About },
  { path: '/css', component: CSS, children: [
    { path: 'layout', component: Layout },
    { path: 'animation', component: Animation },
    { path: 'optimization', component: Optimization }
  ]},
  { path: '/js', component: JS, children: [
    { path: 'closure', component: Closure },
    { path: 'prototype', component: Prototype },
    { path: 'eventloop', component: EventLoop },
    { path: 'toolfunction', component: ToolFunction }
  ] }
]

const router = new VueRouter({
  routes
})

new Vue({
  render: h => h(App),
  router
}).$mount('#app')
